@import url("https://fonts.googleapis.com/css?family=Petit+Formal+Script|Quicksand&display=swap");
article, aside, details, figcaption, figure, footer, header, hgroup, nav, section, summary {
  display: block;
}

audio, canvas, video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

[hidden] {
  display: none;
}

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

a:focus {
  outline: thin dotted;
}

a:active, a:hover {
  outline: 0;
}

h1 {
  font-size: 2em;
}

abbr[title] {
  border-bottom: 1px dotted;
}

b, strong {
  font-weight: 700;
}

dfn {
  font-style: italic;
}

mark {
  background: #ff0;
  color: #000;
}

code, kbd, pre, samp {
  font-family: monospace, serif;
  font-size: 1em;
}

pre {
  white-space: pre-wrap;
  word-wrap: break-word;
}

q {
  quotes: \201C \201D \2018 \2019;
}

small {
  font-size: 80%;
}

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -.5em;
}

sub {
  bottom: -.25em;
}

img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: .35em .625em .75em;
}

button, input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
}

button, input {
  line-height: normal;
}

button, html input[type=button], input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled], input[disabled] {
  cursor: default;
}

input[type=checkbox], input[type=radio] {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0;
}

input[type=search] {
  -webkit-appearance: textfield;
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
}

input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

textarea {
  overflow: auto;
  vertical-align: top;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body, figure {
  margin: 0;
}

legend, button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: '';
  clear: both;
  height: 0;
}

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.visuallyHidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0);
  overflow: hidden;
}

html {
  font-size: 125%;
}

body {
  background-color: #550000;
  font-family: "Quicksand", sans-serif;
  color: #faf6f0;
}

.wrapper {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

button {
  background: none;
  border: none;
  font-weight: bold;
}

h1,
h2,
ul,
li,
p {
  margin: 0;
  padding: 0;
}

ul,
li {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  display: block;
  max-width: 100%;
}

h1,
h2 {
  font-family: "Petit Formal Script", cursive;
}

h1 {
  font-size: 1.7rem;
}

h2 {
  font-size: 1.35rem;
}

header {
  height: 30vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  position: relative;
}

header nav {
  position: absolute;
  top: 10%;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

header nav .logo {
  font-family: "Petit Formal Script", cursive;
  font-weight: bold;
  text-shadow: 2px 2px #ff4800;
}

header ul {
  width: 50%;
  padding: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

header h1 {
  margin: 0 auto;
}

@media (min-width: 750px) {
  header nav ul {
    width: 25%;
  }
}

main {
  position: relative;
}

@media (min-width: 1000px) {
  main ul {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
  main ul li {
    width: 50%;
    padding: 1% 0;
  }
  main ul li:nth-of-type(2n+1) {
    padding-right: 1%;
  }
  main ul li:nth-of-type(2n) {
    padding-left: 1%;
  }
  main ul li .addressContainer {
    height: 100%;
  }
}

.sortLabel {
  display: block;
}

@media (min-width: 750px) {
  .sortLabel {
    margin: 0;
  }
}

@media (min-width: 1000px) {
  .sortLabel {
    display: inline-block;
  }
}

.sortButton {
  border: 2px solid #faf6f0;
  border-radius: 10px;
  color: #131615;
  font-weight: bold;
  background-color: #ffbea7;
  padding: 5px 10px;
  margin: 5px 0;
  display: block;
}

.sortButton:hover, .sortButton:focus {
  -webkit-box-shadow: 5px 5px #ff4800;
          box-shadow: 5px 5px #ff4800;
}

@media (min-width: 750px) {
  .sortButton {
    display: inline-block;
    margin: 5px;
  }
}

button.newAddy {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: -55px;
  right: 5px;
}

button.newAddy i {
  color: #ff4800;
  font-size: 2rem;
}

button.newAddy:hover::before, button.newAddy:focus::before {
  content: 'New contact';
  color: #faf6f0;
  font-size: 1.2rem;
  position: relative;
  top: -7px;
  right: 10px;
}

form {
  background-color: #ffbea7;
  border-radius: 10px;
  padding: 2%;
  z-index: 10;
}

form label {
  color: #550000;
}

form input {
  width: 100%;
  border: 1px solid #550000;
  border-radius: 10px;
  margin-bottom: 10px 0;
  padding: 10px;
  background-color: #faf6f0;
  color: #131615;
}

form input:focus {
  background-image: none;
  -webkit-box-shadow: 5px 5px #ff4800;
          box-shadow: 5px 5px #ff4800;
}

form button {
  display: block;
  margin: 10px auto;
  padding: 10px 20px;
  border: 1px solid #131615;
  border-radius: 10px;
  background-color: #ff4800;
  color: #faf6f0;
}

form button:hover, form button:focus {
  -webkit-box-shadow: 5px 5px #550000;
          box-shadow: 5px 5px #550000;
}

form p {
  color: #550000;
}

@media (min-width: 1000px) {
  form {
    width: 50%;
  }
}

form.newContact {
  position: absolute;
  right: 50px;
  top: -10px;
  width: calc(100% - 100px);
  border: 2px solid #131615;
  overflow: hidden;
  -webkit-animation: slideOut 1s;
          animation: slideOut 1s;
}

form.newContact label {
  display: inline-block;
  margin-top: 15px;
}

@-webkit-keyframes slideOut {
  from {
    height: 0;
    width: 0;
  }
  to {
    height: 515px;
    width: calc(100% - 100px);
  }
}

@keyframes slideOut {
  from {
    height: 0;
    width: 0;
  }
  to {
    height: 515px;
    width: calc(100% - 100px);
  }
}

form.editForm {
  margin: 10px 0;
  border: 2px solid #ff4800;
  position: relative;
}

form.editForm label {
  display: block;
  width: 50%;
  text-align: right;
  padding-right: 20px;
  position: relative;
  top: 25px;
  left: 50%;
  margin: -10px 0;
}

@media (min-width: 1000px) {
  form.editForm {
    position: absolute;
  }
}

.addressContainer {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 30% 70%;
      grid-template-columns: 30% 70%;
  -ms-grid-rows: 15% 15% (calc(70% / 4))[4];
      grid-template-rows: 15% 15% repeat(4, calc(70% / 4));
  margin: 2% 0;
  padding: 3%;
  width: 100%;
  height: auto;
  border-radius: 10px;
  border: 2px solid #ff4800;
  background-color: #ffbea7;
  color: #131615;
  position: relative;
}

.addressContainer .profilePic {
  -ms-grid-row: 1;
  -ms-grid-row-span: 2;
  grid-row: 1 / 3;
}

.addressContainer .profilePic img {
  background-color: #faf6f0;
  border-radius: 50%;
  padding: 2%;
  border: 1px solid #131615;
}

.addressContainer h2 {
  color: #550000;
}

.addressContainer .contactInfo {
  -ms-grid-column: 1;
  -ms-grid-column-span: 2;
  grid-column: 1 / 3;
}

.addressContainer .contactInfo ~ p {
  -ms-grid-column: 1;
  -ms-grid-column-span: 2;
  grid-column: 1 / 3;
}

.addressContainer span {
  font-weight: bold;
}

.addressContainer i {
  color: #550000;
  margin-right: 2%;
}

.addressContainer .deleteThis {
  text-align: left;
  -ms-grid-column: 1;
  grid-column: 1;
}

.addressContainer .update {
  -ms-grid-column: 2;
  grid-column: 2;
  text-align: right;
}

@media (min-width: 500px) {
  .addressContainer h2 {
    text-align: left;
    margin-left: 2%;
  }
  .addressContainer .contactInfo {
    -ms-grid-column: 2;
    -ms-grid-column-span: 1;
    grid-column: 2 / 3;
    -ms-grid-row: 2;
    -ms-grid-row-span: 1;
    grid-row: 2 / 3;
    margin-left: 2%;
  }
}

@media (min-width: 750px) {
  .addressContainer .profilePic {
    grid-row: span 5;
  }
  .addressContainer .contactInfo,
  .addressContainer .contactInfo ~ p {
    -ms-grid-column: 2;
    -ms-grid-column-span: 1;
    grid-column: 2 / 3;
    margin-left: 2%;
  }
}

@media (min-width: 1000px) {
  .addressContainer {
    -ms-grid-rows: (1fr)[6];
        grid-template-rows: repeat(6, 1fr);
  }
  .addressContainer .profilePic {
    -ms-grid-row: 1;
    -ms-grid-row-span: 5;
    grid-row: 1 / span 5;
  }
  .addressContainer p {
    margin-left: 2%;
  }
}

footer {
  padding-top: 2%;
  text-align: center;
}
